.video {
  z-index: 1;
  position: relative;
  width: 482px;
  height: 317px;
}
.video iframe {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
.video.is-active .video__player {
  display: block;
}
.video.is-active .video__control {
  display: none;
}

.video__control {
  font-size: 0;
  line-height: 0;
  margin: 0;
  padding: 0;
  position: absolute;
  cursor: pointer;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  background-size: cover;
  background-color: #2d383f;
  background-image: url("../img/video-img.webp"), url("../img/video-img.png");
  border: none;
}
@media (min-resolution: 2dppx) {
  .video__control {
    background-image: url("../img/video-img@2x.webp"), url("../img/video-img@2x.png");
  }
}
.video__control::after {
  position: absolute;
  content: "";
  width: 56px;
  height: 56px;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-image: url("../img/button-video.svg");
  background-size: cover;
}
@media (max-width: 1199px) {
  .video__control::after {
    width: 72px;
    height: 72px;
  }
}
@media (hover: hover) {
  .video__control:hover:not(:disabled)::after {
    background-image: url("../img/button-video-focus.svg");
  }
}
.video__control:focus:not(:disabled)::after {
  background-image: url("../img/button-video-focus.svg");
}
.video__control:disabled::after {
  background-image: url("../img/button-video-disabled.svg");
}
@media (hover: hover) {
  .video__control:active:not(:disabled)::after {
    background-image: url("../img/button-video-active.svg");
  }
}

.video__player {
  display: none;
}

