@mixin reset-style {
  margin: 0;
  padding: 0;
}

@mixin reset-list {
  @include reset-style;
  list-style: none;
}

@mixin font-reset {
  font-size: 0;
  line-height: 0;
}

@mixin retina {
  @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
    @content;
  }
}

@mixin hover {
  @media (hover: hover) {
    &:hover:not(:disabled) {
      @content;
    }
  }
}

@mixin focus {
  &:focus:not(:disabled) {
    @content;
  }
}

@mixin hover-focus {
  @media (hover: hover) {
    &:hover:not(:disabled) {
      @content;
    }
  }

  &:focus:not(:disabled) {
    @content;
  }
}

@mixin active {
  @media (hover: hover) {
    &:active:not(:disabled) {
      @content;
    }
  }
}

// Desktop first

@mixin vp-1199 {
  @media (max-width: ($vp-1200 - 1px)) {
    @content;
  }
}

@mixin vp-767 {
  @media (max-width: ($vp-768 - 1px)) {
    @content;
  }
}

